import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasAdministradoraMHO } from '../../../../dependencies/administradoraMHODependency';
import { CLASS_NAME, ContaPagarMHO } from '../../../../models/dtos/mho/contaPagarMHO';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'descricao',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ContaPagarMHOSearchView: FC<SearchViewProps<ContaPagarMHO>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'dataVencimento',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. Vencimento',
    },
    {
      dataField: 'historico.descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Histórico',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'valor',
      editable: true,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Valor',
    },
    {
      dataField: 'dataPagamento',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. Pagamento',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Contas a Pagar" dependencies={[() => hasAdministradoraMHO(globalReducer.globalParameter)]} wikiId={1109}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'ContaPagarMHOEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ContaPagarMHOSearchView;
